<template>
  <v-app-bar app dense fixed>
    <!-- Menu button. -->
    <v-app-bar-nav-icon @click="setOpenPanelServiceList(!openPanelServiceList)"></v-app-bar-nav-icon>
    <v-spacer></v-spacer>
    <!-- Reload panel button. -->
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn icon v-bind="attrs" v-on="on" :href="homeUrl">
          <v-icon>mdi-reload</v-icon>
        </v-btn>
      </template>
      <span>{{ $t("message.0") }}</span>
    </v-tooltip>
    <!-- Light or dark theme button. -->
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn icon v-bind="attrs" v-on="on" @click="$vuetify.theme.dark = !$vuetify.theme.dark">
          <v-icon>mdi-brightness-4</v-icon>
        </v-btn>
      </template>
      <span>{{ $t("message.1") }}</span>
    </v-tooltip>
    <v-spacer></v-spacer>
    <!-- Logout button. -->
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn icon v-bind="attrs" v-on="on" @click="logout">
          <v-icon>mdi-logout</v-icon>
        </v-btn>
      </template>
      <span>{{ $t("message.2") }}</span>
    </v-tooltip>
  </v-app-bar>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  name: 'AppBar',

  data: () => ({
    homeUrl: process.env.NODE_ENV === 'production' ? '/admin' : '/',
    // Open and close service list panel (navigation-drawer).
    panelServiceList: null
  }),

  computed: {
    ...mapState([
      'openPanelServiceList'
    ])
  },

  methods: {
    ...mapMutations([
      'setUsername',
      'setIsAuthenticated',
      'setOpenPanelServiceList'
    ]),
    logout() {
      this.axios.post('/admin/logout', {})
        .then(response => {
          this.setUsername('..')
          this.setIsAuthenticated(false)
        })
        .catch(error => {
          console.log(error)
        })
    }
  }
}
</script>
